<template>
  <div>
    <header class="logo-header">
      <div class="logo-container">
        <!-- 로고 이미지 -->
        <router-link to="/C_MainCategory">
          <img
            src="../assets/img/dutchOrder_colored.png"
            alt="Logo"
            class="logo-image"
          />
        </router-link>
        <!-- 홈페이지 이름 -->
        <span class="site-name">더치오더</span>
      </div>
    </header>
    <header class="nav-header">
      <div v-if="userLevel === 'member'" class="nav-toggle" @click="toggleNav">
        <img src="../assets/img//menu.png" class="menu-icon" />
      </div>

      <router-link to="/B_Ordering">민서 사업자화면</router-link>
      <router-link to="/business/join">민서 사업자회원가입</router-link>
      <router-link to="/B_ShopTimeUpdate">민서 사업자수정</router-link>

      <!-- 사용자 레벨이 admin인 경우 -->
      <template v-if="userLevel === 'admin'">
        <router-link to="/admin"></router-link>
        <router-link to="/admin/orderState">주문현황</router-link>
        <router-link to="/admin/reviewBoard">리뷰관리하기</router-link>
        <router-link to="/admin/notice">공지사항</router-link>
        <router-link to="/login">로그인</router-link>
      </template>
      <!-- 사용자 레벨이 business인 경우 -->
      <template v-else-if="userLevel === 'business'">
        <router-link to="/business"></router-link>
        <router-link to="/business/orderState">주문현황</router-link>
        <router-link to="/business/reviewBoard">리뷰관리하기</router-link>
        <router-link to="/business/notice">공지사항</router-link>
        <router-link to="/login">로그인</router-link>
      </template>
      <!-- 사용자 레벨이 member인 경우 -->
      <template v-else>
        <router-link to="/member"></router-link>
        <router-link to="/member/updateInfo">정보수정</router-link>
        <router-link to="/member/myFriend">내친구확인</router-link>
        <router-link to="/member/myCart">장바구니</router-link>
        <router-link to="/member/myOrder">주문내역</router-link>
        <router-link to="/member/notice">공지사항</router-link>
        <router-link to="/login">로그인</router-link>
      </template>
    </header>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { computed } from "vue";
import { useStore } from "vuex";

const store = useStore();

const userLevel = computed(() => {
  const user = store.getters.getUser;
  return user ? user.level : null;
});

const isNavOpen = ref(false);

const toggleNav = () => {
  isNavOpen.value = !isNavOpen.value;
};
</script>

<style scoped>
.logo-header,
.nav-header {
  background-color: #fff9f4;
  color: #242424;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-container {
  display: flex;
  align-items: center;
}

.logo-image {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.site-name {
  font-size: 1.2rem;
}

.nav-menu {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: -100%; /* 초기에는 오른쪽으로 숨겨놓음 */
  transition: right 0.3s ease-in-out;
}

.nav-menu-open {
  right: 0; /* 열렸을 때는 오른쪽에서 슬라이드됨 */
}

.menu-icon {
  width: 40px;
  height: 40px;
  cursor: pointer;
}
</style>